export const createImagesMarkup = (arr) => {
  return arr
    .map((el) => {
      const {
        preview: previewUrl,
        original: originalUrl,
        description: alt,
      } = el;
      return `
        <li class="gallery__item">
            <a class="gallery__link" href="${originalUrl}">
                <img
                class="gallery__image"
                src="${previewUrl}"
                data-source="${originalUrl}"
                alt="${alt}"
                />
            </a>
        </li>
        `;
    })
    .join("");
};

