export const preventDefaultImageLinks = (refs = {}) => {
  refs.imageLinks = document.querySelectorAll(".gallery__link");
  refs.imageLinks.forEach((link) => {
    link.addEventListener("click", (e) => {
      e.preventDefault();
    });
  });
};
